import React from 'react';
import './InputForm.css';

export default class InputForm extends React.Component{
    
    state = {
        placeholder:"Add the task..."
    }


    render(){
        return(
            <div>        
                <input  placeholder={this.state.placeholder}
                        className="inpClass"
                        onChange={this.props.inputFill}
                        onFocus={(e)=>{this.setState({placeholder:''})}} 
                        onBlur={(e)=>{this.setState({placeholder:"Add the task..."})}}
                        onKeyPress={this.props.keyPress}
                        value={this.props.inputValue}/>
                <i className="fa fa-times delAllBtn"
                   onClick={()=>{this.props.delAllElements()}} />
            </div>
        )
    }
}