import React from 'react';
import './AddTask.css';

export default class AddTask extends React.Component{
    
    render({tasks, deleteElement}=this.props){
        
      //  let a = Math.random(0,1)*10^9;
   
        let classNames = "itemStyle";
        if (tasks.done) {
            classNames += ' done';
          }

        let addTask = tasks.map((item, id)=>{  
            return(
                <div>
                    <ul className="itemListStyle" >
                        <li key={tasks.id} 
                            className={classNames}
                            onClick={()=>{this.props.onToggleDone(id)}}>
                            {id+1}. {item.label} 
                            <i className="fa fa-times delBtn" 
                                onClick={() => deleteElement(id)}/>
                        </li>
                    </ul>
                </div>
            )
        })
        return(
            <div>
                {addTask}
            </div>
        )
    }
}