import React from 'react';
import './App.css';
import AddTask from './AddTask.js';
import InputForm from './InputForm.js';

export default class App extends React.Component{
  cnt = 1;

  state = {
    tasks:[],
    try:"test",
    inputValue:'',
    time:''
  }

  inputFill = (e) =>{
    //+(e.target.value.replace(/[^+\d]/g, ''))
      this.setState({
          inputValue:e.target.value
      })
  }
  addElement = (e) =>{
    const newItem = {
      label: this.state.inputValue,
      important: false,
      done:true,
      id: this.cnt++
    };

    if(e.key === 'Enter'&& this.state.inputValue!==''){
      let newArr = this.state.tasks;
      newArr.push(newItem);
      this.setState({
        tasks:newArr,
        inputValue:'',
      }) 
    }
  }

  deleteElement = (id) =>{
    let newArr = this.state.tasks;
    newArr.splice(id,1);
    this.setState({
      tasks:newArr
    })
  }
  
  deleteAllElements = (id) =>{
    let newArr = [];
    this.setState({
      tasks:newArr
    })
  }

  markAsDone = (id) =>{
/*
    let a = this.state.tasks[id].done;
    let newArr = 
    this.setState({
      tasks: newArr
    })*/
  }

  render(){
    return(
      <div className="appStyle">
        <InputForm
          inputFill={this.inputFill}
          keyPress={this.addElement}
          inputValue={this.state.inputValue}
          delAllElements={this.deleteAllElements}/>
        <AddTask 
          tasks={this.state.tasks}
          deleteElement={this.deleteElement}
          onToggleDone={this.markAsDone}/>    
      </div>
    )
  }

}





